import { Component, Input, OnChanges, SimpleChanges, OnInit, ViewChild } from '@angular/core';
import { SDGNameDictionary, SDGColorDictionary } from '../../../model/sdg.enum';
import { Thin2Chart } from 'thin2-chart';

@Component({
    selector: 'pie-chart-component',
    templateUrl: './pie-chart.component.html'
})
export class PieChartComponent implements OnChanges {
    @ViewChild(Thin2Chart)
    public chart: Thin2Chart;

    @Input()
    public dataset: any[];

    // Angular  ngOnChanges 
    //  SimpleChanges 
    public ngOnChanges(changes: SimpleChanges): void {
        this.dataset = changes.dataset.currentValue;
        if (this.dataset) {
            this.dataset = this.dataset.map(this.datasetAdapter);
            this.drawChart();
        }
    }

    public drawChart() {
        const options = {
            chart: {
                plotBackgroundColor: null,
                plotBorderWidth: null,
                plotShadow: false,
                type: 'pie'
            },
            title: {
                text: ''
            },
            tooltip: {
                formatter() {
                    return `<b>${this.point.name}. ${
                        SDGNameDictionary[this.point.name]
                    }</b> <br> <b>Total news: ${this.y}</b>`;
                }
            },
            accessibility: {
                point: {
                    valueSuffix: ''
                }
            },
            plotOptions: {
                pie: {
                    allowPointSelect: true,
                    cursor: 'pointer',
                    dataLabels: {
                        enabled: true,
                        formatter() {
                            return `<b>${this.point.name}. ${SDGNameDictionary[this.point.name]} (${
                                this.y
                            })</b>`;
                        }
                        // format: '<b>SDG: {point.name} ({y})</b>'
                    }
                }
            },
            series: [
                {
                    name: '',
                    data: this.dataset
                }
            ]
        };

        this.chart.draw(options);
    }

    // StatisticsODS
    public datasetAdapter = (item) => ({
        name: item.ods,
        color: SDGColorDictionary[item.ods],
        y: Number(item.total)
    });
}
